@import './Game.scss';
@import './hex.scss';
@import './pieces.scss';
@import './z-index.scss';

.app-content {
  position: relative;
}

.rjsf {
  .checkboxes {
    @apply flex flex-wrap;
    .checkbox {
      @apply pr-4 w-1/2;
    }
  }
}

.app-content.debug {
  [data-index]:after {
    content: attr(data-index);
  }
  .hex-player_2[data-index]:after {
    background: rgba(255,255,255,0.45);
  }
}